import { useReducer } from 'react';
import {
	UPDATE_PRODUCTS,
	UPDATE_SERVICES,
	ADD_TO_CART,
	UPDATE_CART_QUANTITY,
	REMOVE_FROM_CART,
	ADD_MULTIPLE_TO_CART,
	UPDATE_CATEGORIES,
	UPDATE_CURRENT_CATEGORY,
	CLEAR_CART,
	TOGGLE_CART,
} from './actions';

export const init = (initialState) => {
	return {state: initialState}
}
export const reducer = (state, action) => {
	switch (action.type) {
		case UPDATE_PRODUCTS:
			return {
				...state,
				products: [...action.products],
			};

		case UPDATE_SERVICES:
			console.log('action', action);
			return {
				...state,
				services: [...action.services],
			};

		case ADD_TO_CART:
			return {
				...state,
				cartOpen: true,
				cart: [...state.cart, action.product, action.services],
			};

		case ADD_MULTIPLE_TO_CART:
			return {
				...state,
				cart: [ ...action.products, ...action.services],
			};

		case UPDATE_CART_QUANTITY:
			return {
				...state,
				cartOpen: true,
				cart: state.cart.map(
					(product) => {
						if (action._id === product._id) {
							product.purchaseQuantity = action.purchaseQuantity;
						}
						return product;
					},
					(service) => {
						if (action._id === service._id) {
							service.purchaseQuantity = action.purchaseQuantity;
						}
						return service;
					}
				),
			};

		case REMOVE_FROM_CART:
			let newState = state.cart.filter(
				(product) => {
					return product._id !== action._id;
				},
				(service) => {
					return service._id !== action._id;
				}
			);

			return {
				...state,
				cartOpen: newState.length > 0,
				cart: newState,
			};

		case CLEAR_CART:
			return {
				...state,
				cartOpen: false,
				cart: [],
			};

		case TOGGLE_CART:
			return {
				...state,
				cartOpen: !state.cartOpen,
			};

		case UPDATE_CATEGORIES:
			return {
				...state,
				categories: [...action.categories],
			};

		case UPDATE_CURRENT_CATEGORY:
			return {
				...state,
				currentCategory: action.currentCategory,
			};

		default:
			return init(state);
	}
};

export function useStoreReducer(initialState) {
	return useReducer(reducer, initialState, init);
}
