import { gql } from '@apollo/client';

export const LOGIN = gql`
	mutation login($email: String!, $password: String!) {
		login(email: $email, password: $password) {
			token
			user {
				_id
			}
		}
	}
`;

export const ADD_ORDER = gql`
	mutation addOrder($products: [ID]!, $services: [ID]!) {
		addOrder(products: $products, services: $services) {
			purchaseDate
			products {
				_id
				name
				description
				price
				quantity
				category {
					name
				}
			}
			services {
				_id
				name
				description
				price
				quantity
				category {
					name
				}
			}
		}
	}
`;

export const ADD_USER = gql`
	mutation addUser(
		$firstName: String!
		$username: String!
		$email: String!
		$password: String!
	) {
		addUser(
			firstName: $firstName
			username: $username
			email: $email
			password: $password
		) {
			token
			user {
				_id
			}
		}
	}
`;
